import React from "react";
import { HashRouter, Route, Switch } from "react-router-dom";
import Frontpage from "./Components/Frontpage/Frontpage";
import BookNow from "./Components/UserPage/BookNow"
import BookQR from "./Components/UserPage/BookQR"
import DashBoard from "./Components/UserPage/DashBoard"
import TraceBooking from './Components/UserPage/TraceBooking'
import EarnwithusDashboard from './Components/EarnWithUs/EarnwithusDashboard'
import EarnNowFormPage from "./Components/EarnWithUs/EarnNowFormPage";

const App = () => {
  return (
    <HashRouter>
      <Switch>
        <Route exact path="/" component={Frontpage} />
        <Route path="/booknowformpage" component={BookNow} />
        <Route path="/dashboard" component={DashBoard}/>
        <Route path="/tracenowformpage" component={TraceBooking}/>
        <Route path="/EarnwithusDashboard" component={EarnwithusDashboard}/>
        <Route path="/Earnnowformpage" component={EarnNowFormPage}/>
        <Route path="/BookQR" component={BookQR}/>

      </Switch>
    </HashRouter>
  );
};

export default App;
