const IntroDiv = () => {
  const [opendatadiv, setopendatadiv] = React.useState(false);
  const [datapicarr,setdatapicarr]=React.useState([]);
  // eslint-disable-next-line
  const [bttype, setbttype] = React.useState("");
  // const [modalStyle] = React.useState(getModalStyle);
  // const classes = useStyles();

  const message = [
    "Home Service",
    "Vehicle Inspection",
    "Vehicle Auto-Parts",
    "Teflon Polish",
    "Rubbing Polish",
    "Dry Cleaning",
    "Engine Coating",
    "Anti-Rat Treatment",
    "AC Duct Foam Cleaner",
  ];
  const onbottondiv = (e) => {
    if (opendatadiv) {
      setopendatadiv(false);
      setbttype("");
      setdatapicarr([])
    } else {
      setopendatadiv(true);
      setbttype(e);
      setdatapicarr(datapic.data[e-1].data)
    } 

   // console.log(datapic.data[e-1].data)
  
  };

  return (
    <>
      <div className="IntroDivMain">
        <div className="divfront1">
          <Grid
            container
            direction="row"
            justify="space-around"
            alignItems="center"
          >
            <div
              style={{
                width: 200,
                height: 200,
                margin: 10,
                display: "flex",
                flexDirection: "column",
              }}
            >
              <span
                style={{
                  fontSize: 26,
                  fontWeight: "550",
                  color: "#802003",
                  whiteSpace: "nowrap",
                }}
              >
                COSMATIC{" "}
                <span style={{ fontSize: 26, color: "#470101" }}>SERVICES</span>
              </span>
              <span style={{ fontSize: 24, color: "#110038" }}>We Provide</span>
              <span
                className="blacktextshadow"
                style={{
                  color: "#000000",
                  fontFamily: "Nunito,sans-serif",
                  fontSize: 25,
                  marginTop: 5,
                  whiteSpace: "nowrap",
                }}
              >
                <Typewriter
                  options={{
                    strings: message,
                    autoStart: true,
                    loop: true,
                  }}
                />
              </span>
              <Link className="booknowbt" to="/booknowformpage">
                BOOK NOW &nbsp;
                <SendIcon />
              </Link>
              <Link className="Tracknowbt"  to="/tracenowformpage">
                Track Status&nbsp;
                <VisibilityIcon/>           
              </Link>
            </div>

            <div>
              <img id="imgcar" src={Carservicepicjpeg} alt="carservice.jpg" />
            </div>
          </Grid>
        </div>
      </div>
      <div className="divfront2">
        <img id="pichori" src={Pichori} alt="pic hori" />
      </div>
      <Ourservices onbottondiv={onbottondiv} datapicarr={datapicarr} bttype={bttype} opendatadiv={opendatadiv}/>
      <EarnWithUS/>
      <Reachus/>
    </>
  );
};

export default IntroDiv;
