import React from "react";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import EarnwithusDetailsDiv from "./EarnwithusDetailsDiv";
import Axios from "axios";
import urlinit from "../ServerAddress";
import FilterDiv from "./FilterDiv";
import CheckReffConfig from "./CheckReffConfig"
import CircularProgress from "@material-ui/core/CircularProgress";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

function EarnwithusDashboard() {
  // eslint-disable-next-line
  const [name] = React.useState(localStorage.refname);// eslint-disable-next-line
  const [mobno] = React.useState(localStorage.refmob);// eslint-disable-next-line
  const [add] = React.useState(localStorage.refadd);// eslint-disable-next-line
  const [refid] = React.useState(localStorage.refid);
  const [refcode] = React.useState(localStorage.refcode);
  // eslint-disable-next-line
  const [buss] = React.useState(localStorage.refbuss);
  const [data, setdata] = React.useState([]);
  const [showvalue, setshowValue] = React.useState("2");
  const [showmoredivp, setshowmoredivp] = React.useState(false);
  const [totearnam,settotearnam]=React.useState(0)
  const [backdrop, setBackdrop] = React.useState(true);

  const classes = useStyles();
  const showvaluehandleChange = (event) => {
    setshowValue(event);
    console.log(showvalue);
  };

  React.useEffect(() => {
    getdata();
      // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getdata = () => {
    Axios.post(urlinit + "/api/refpromodet", {
      promo: refcode,
    }).then((res) => {
      setdata(res.data);
      console.log(res.data);
      setBackdrop(false)
    });
  };
   let amount=0;
  const totearn = (value) =>{

      amount=amount+value;
      settotearnam(amount)
      console.log(totearnam)

  }

  const showmorediv = () => {
      showmoredivp?      setshowmoredivp(false)
      :      setshowmoredivp(true)
  };

  return (
    <>
      <Header />
      <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <div style={{padding:1,backgroundImage: "linear-gradient(#f5b278, #f5e589)"}}>
      <FilterDiv
        showmorediv={showmorediv}
        showvalue={showvalue}
        showvaluehandleChange={showvaluehandleChange}
        showmoredivp={showmoredivp}
        amount={totearnam}
      />
      {showmoredivp ? <CheckReffConfig/> : null}
      {data
        ? data.map((items, i) => {
            return (
              <EarnwithusDetailsDiv
                showvalue={showvalue}
                formid={items.sfid}
                sername={items.name}
                status={items.status}
                carmodel={items.carmodel}
                carcompany={items.carcompany}
                date={items.date}
                finalprice={items.finalprice}
                promo={refcode}
                sertype={items.serviceid}
                serdonedate={items.serdonedate}
                reffpaydate={items.reffpaydate}
                reffpaystatus={items.reffpaystatus}
                totearn={totearn}
              />
            );
          })
        : null}
        </div>
      <Footer />
    </>
  );
}

export default EarnwithusDashboard;
