import React from "react";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import "./EarnWithUS.css";
import EarnWithUSPic from "../Addons/earnwithus.jpg";
import Axios from "axios";
import CircularProgress from "@material-ui/core/CircularProgress";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";
import urlinit from "../ServerAddress";
import EarnwithusDashboard from "./EarnwithusDashboard";
import { Redirect } from "react-router-dom";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const EarnNowFormPage = () => {
  const [mobno, setmobno] = React.useState("");
  const [pass, setpass] = React.useState("");
  const [backdrop, setBackdrop] = React.useState(false);
  const [showdet, setshowdet] = React.useState(false);
  const [userauthp, setuserauth] = React.useState(false);

  const classes = useStyles();

  const onchangehandler = (event) => {
    let nam = event.target.name;
    let val = event.target.value;

    if (nam === "mobno") {
      setmobno(val);
    }
    if (nam === "pwd") {
      setpass(val);
    }
  };
  const onregsubmitexistuser = (event) => {
    event.preventDefault();
    console.log(mobno, pass);
    setBackdrop(true);
    Axios.post(urlinit + "/api/EarnWithUsvalidate", {
      mobno: mobno,
      pass: pass,
    }).then((res) => {
   //   console.log(res.data)
      if (res.data.length ===0) {
        setshowdet(true);
        setBackdrop(false);
      } else {
        localStorage.setItem("refid", res.data[0].id);
        localStorage.setItem("refname", res.data[0].name);
        localStorage.setItem("refadd", res.data[0].address);
        localStorage.setItem("refbuss", res.data[0].bussiness);
        localStorage.setItem("refcode", res.data[0].refid);
        localStorage.setItem("refmob", res.data[0].mobno);
        setuserauth(true)
      }
    
    });
  };

  const userauth = () =>{
    return (
        <>
          <Redirect
            to="/EarnwithusDashboard"
            Component={EarnwithusDashboard}
          />
        </>
      );
  }
  const Formpage = () => {
    return (
      <div>
        {showdet ? (
          <span style={{ color: "red" }}>Invalid Mobile No/Password</span>
        ) : null}
        <form onSubmit={onregsubmitexistuser} id="create-course-form">
          <input
            type="text"
            name="mobno"
            id="mobno"
            placeholder="Mobile no"
            required
            pattern="[6789][0-9]{9}"
            title="Enter Valid Mobile No"
            onChange={onchangehandler}
          />
          <input
            type="password"
            name="pwd"
           
            required
            text="hidden"
            minLength="8"
            onChange={onchangehandler}
            placeholder="Password"
          />
          <input type="submit" text="SUBMIT" id="regsubmit" name="regsubmit" />
          <input type="reset" text="RESET" id="regreset" name="regreset" />
        </form>
        <br />
        <span>
          For Registration,Password Reset and other Query Contact-us at{" "}
        </span>
        <ul>
          <li>+91 9568987517</li>
          <li>+91 7974392984</li>
        </ul>
        <img style={{ width: "100%" }} alt ="earnwithuspic" src={EarnWithUSPic} />
      </div>
    );
  };
  const backdropfun = () =>{
      return (
        <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
      )
  }
  return (
    <>
    {
        userauthp?userauth():null
    }
      {backdropfun()}
      <Header />
    
      <div className="Formpagemain">
        <div className="Formdivmain">
          <span style={{ fontSize: 25, fontWeight: 500 }}>
            Become An Ambassador
          </span>
          <br />
          <br />
          {Formpage()}
        </div>
      </div>

      <Footer />
    </>
  );
};

export default EarnNowFormPage;
