import React from "react";
import Cancel from "../UserPage/icon/cancel.png";
import Checked from "../UserPage/icon/checked.png";
import Process from "../UserPage/icon/process.png";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";

function EarnwithusDetailsDiv(props) {
  const srcimg = () => {
    if (props.status === 1) {
      return Process;
    }
    if (props.status === 2) {
      return Checked;
    }
    if (props.status === 3) {
      return Cancel;
    }
  };


  const totamountfun = () =>{
    if (props.sertype === 1) {
        props.totearn(200);
      } else {
        props.totearn(100);
      }
  }

  const moneycal = () => {
    if (props.sertype === 1) {
      return "200";
    } else {
      return "100";
    }
  };

  return parseInt(props.showvalue) === props.status || parseInt(props.showvalue) === 4? (
    <div className="listdiv">
      <div style={{ marginBottom: 15 }}>
        <Grid container>
          <Grid item xs={4}>
            <span style={{ fontSize: 16 }}>Service ID :- {props.formid}</span>
          </Grid>
          <Grid item xs={8}>
            <span style={{ fontSize: 18, fontWeight: 450 }}>
              {props.sername}
            </span>
          </Grid>
        </Grid>
        <Divider />
      </div>
      <Grid container>
        <Grid item xs={3}>
          <img src={srcimg()} alt="ss" className="imglist" />
        </Grid>
        <Grid item xs={5}>
          <Grid
            container
            direction="column"
            justify="space-evenly"
            alignItems="flex-start"
          >
            <span style={{ fontSize: 22, fontWeight: 500 }}>
              {props.carmodel}
            </span>
            <span>{props.carcompany}</span>
          </Grid>
        </Grid>
        <Grid item xs={4}>
          <Grid
            container
            direction="column"
            justify="space-evenly"
            alignItems="flex-start"
          >
            <span style={{ fontSize: 22, fontWeight: 500 }}>{moneycal()}₹</span>
            {props.promo ? (
              <>
                <span style={{ whiteSpace: "nowrap" }}>Promo Used:-</span>
                <span>{props.promo}</span>
              </>
            ) : null}
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <Divider />
          <span>Registered On:- {props.date}</span>
        </Grid>
        {props.serdonedate !== null ? (
          props.reffpaystatus === 1 ? (
            
            <>
            {totamountfun()}
              <Grid item xs={12}>
                <br />
                <br />
                <Divider />
                <br />
                <span style={{ color: "green", fontSize: 20 }}>
                  Paid On:- {props.reffpaydate}
                </span>

                <Divider />
                <span>
                  Wait For 72 hours after Successful payment by user for
                  service,If Payment Not Received after 72 hours you can
                  contact-us.
                </span>
              </Grid>
            </>
          ) : null
        ) : null}
      </Grid>
    </div>
  ) : null;
}

export default EarnwithusDetailsDiv;
