import React from "react";
import Divider from "@material-ui/core/Divider";
import urlinit from "../ServerAddress";
import Axios from "axios";
import "./reffcss.css";

const Referralwindow = (props) => {
  const [data, setdata] = React.useState([]);
  const [data2, setdata2] = React.useState([]);

  const myFunction = () => {
    var copyText = document.getElementById("promoid");
    var textArea = document.createElement("textarea");
    textArea.value = copyText.textContent;
    document.body.appendChild(textArea);
    textArea.select();
    document.execCommand("Copy");
    alert("Promo code Copied: " + props.promo);
    textArea.remove();
  };

  React.useEffect(() => {
    getdata();
      // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);

  const getstatus = (st) => {
    if (st === 1) {
      return <span style={{ color: "#003de3" }}>Waiting</span>;
    }
    if (st === 2) {
      return <span style={{ color: "#17b502" }}>Success</span>;
    }
    if (st === 3) {
      return <span style={{ color: "#e30004" }}>Cancelled</span>;
    }
  };
  const getdata = () => {
    Axios.post(urlinit + "/api/promodet", {
      promo: props.promo,
    }).then((res) => {
      console.log("------------>", res.data);
      getavailcode();
      setdata(res.data);
    });
  };
  const getavailcode = () => {
    Axios.post(urlinit + "/api/promodetavail", {
      uid: props.userid,
    }).then((res) => {
      console.log('---->',res.data);
      setdata2(res.data);
    });
  };

  

  const existUser = () =>{
    return (
    <div>
      <div
        style={{
          height: 56,
          width: "vh",
          maxWidth: 400,
          backgroundColor: "#5573ad",
          borderRadius: 6,
          display: "flex",
          margin: 5,
        }}
        onClick={() => {
          myFunction();
        }}
      >
        <div
          style={{
            width: 255,
            textAlign: "center",
            color: "#fff",
            cursor: "pointer",
            fontSize: 14,
            border: "1px dashed hsla(0,0%,100%,.45)",
            margin: "auto",
            padding: "5px 0",
          }}
        >
          <span style={{ fontSize: 18 }}>Referral Code </span>
          <span id="promoid" style={{ fontSize: 20 }}>
            {props.promo}
          </span>
        </div>
      </div>

      <p>
        Share this Code with your friends and both of you will get 5% Discount{" "}
      </p>
      <Divider />
      <table>
        <tbody>
          <tr>
            <th>S.no</th>
            <th>Service</th>
            <th>Date</th>
            <th>Status</th>
          </tr>
          {data.map((items, i) => {
            return (
              <tr>
                <td>{i + 1}</td>
                <td>{items.name}</td>
                <td>{items.date}</td>
                <td>{getstatus(items.status)}</td>
              </tr>
            );
          })}
        </tbody>
      </table>
      <Divider />
      <span style={{ fontSize: 20 }}>Available Coupons </span>
      <table>
        <tbody>
          <tr>
            <th>S.no</th>
            <th>Code</th>
            <th>Discount</th>
          </tr>
          {data2.map((items, i) => {
            return (
              <tr>
                <td>{i + 1}</td>
                <td>{items.newpromocode}</td>
                <td>{items.discountdet}%</td>
              </tr>
            );
          })}
        </tbody>
      </table>
    </div>
  );}


  return ( existUser())
};



export default Referralwindow;
