import React from "react";
import "./EarnWithUS.css";
import Grid from "@material-ui/core/Grid";
var QRCode = require('qrcode.react');

function CheckReffConfig() {
  const myFunction = () => {
    var copyText = document.getElementById("promoid");
    var textArea = document.createElement("textarea");
    textArea.value = copyText.textContent;
    document.body.appendChild(textArea);
    textArea.select();
    document.execCommand("Copy");
    alert("Promo code Copied: " + localStorage.refcode);
    textArea.remove();
  };
  const reffcodediv = () => {
    return (
      <div
        style={{
          height: 56,
          width: "vh",
        //   maxWidth: 400,
          backgroundColor: "#5573ad",
          borderRadius: 6,
          display: "flex",
          margin: 5,
        }}
        onClick={() => {
          myFunction();
        }}
      >
        <div
          style={{
            width: 255,
            textAlign: "center",
            color: "#fff",
            cursor: "pointer",
            fontSize: 14,
            border: "1px dashed hsla(0,0%,100%,.45)",
            margin: "auto",
            padding: "5px 0",
          }}
        >
          <span style={{ fontSize: 18 }}>Referral Code </span>
          <span id="promoid" style={{ fontSize: 20 }}>
            {localStorage.refcode}
          </span>
        </div>
      </div>
    );
  };
  return (
    <div className="CheckReffConfigMain">
      <Grid container>
        <Grid item xs={12} sm={12}>
          <Grid
            container
            alignItems="flex-start"
            direction="row"
            justify="center"
          >
            <span style={{ fontSize: 22, fontWeight: 500, color: "#030633" }}>
              Hello {localStorage.refname}
            </span>
          </Grid>
        </Grid>
        <Grid item xs={12} sm={12}>
          {reffcodediv()}
        </Grid>
        <Grid item xs={12} sm={12}>
        <QRCode value={localStorage.refcode} />
                </Grid>
        
        <table>
          <tbody>
            <tr>
              <td></td>
              <td></td>
            </tr>
            <tr>
              <td>Bussiness Name </td>
              <td>
                <span style={{ fontSize: 18, fontWeight: 450 }}>
                  {localStorage.refbuss}
                </span>
              </td>
            </tr>
            <tr>
              <td> Mobile No </td>
              <td>
                <span style={{ fontSize: 18, fontWeight: 450 }}>
                  {localStorage.refmob}
                </span>
              </td>
            </tr>
            <tr>
              <td>Unique</td>
              <td>
                <span style={{ fontSize: 18, fontWeight: 450 }}>
                  {localStorage.refid}
                </span>
              </td>
            </tr>
            <tr>
              <td>Address</td>
              <td>
                {" "}
                <span style={{ fontSize: 18, fontWeight: 450 }}>
                  {localStorage.refadd}
                </span>
              </td>
            </tr>
          </tbody>
        </table>
      </Grid>
    </div>
  );
}

export default CheckReffConfig;
