import React, { useState } from "react";
import Axios from "axios";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import DashBoard from "./DashBoard";
import CircularProgress from "@material-ui/core/CircularProgress";
import Backdrop from "@material-ui/core/Backdrop";
import { Redirect } from "react-router-dom";
import { makeStyles } from "@material-ui/core/styles";
import urlinit from "../ServerAddress";
//const mod2 = "https://safe-bastion-89212.herokuapp.com/";

//const urlinit = "http://192.168.43.168:5000";

//Define Style
const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const TraceBooking = () => {
  const [mobno, setmobno] = useState("");
  const [backdrop, setBackdrop] = useState(false);
  const [reddash, setreddash] = useState(false);
  const [nodata, setnodata] = useState(false);
  const classes = useStyles();

  const onregsubmitmobno = (event) => {
    event.preventDefault();
    setBackdrop(true);
    document.getElementById("create-course-form").reset();
    Axios.post(urlinit + "/api/getuser", {
      mobno: mobno,
    }).then((res) => {
      if (res.data[0] !== undefined) {
        setmobno(res.data[0].mobileno);
        setBackdrop(false);
        setreddash(true);
      } else {
        setBackdrop(false);
        setnodata(true);
      }
    });
  };

  const redirectdashboard = () => {
    localStorage.setItem("mobno", mobno);
    return (
      <>
        <Redirect to="/dashboard" Component={DashBoard} />
      </>
    );
  };

  const onchangehandler = (ee) => {
    setmobno(ee.target.value);
  };

  const divno = () => {
    return (
      <>
        <div className="booknowmain" style={{ height: "50vh" }}>
          <div className="formdivp">
            <h2>Track Services</h2>
            <span>Enter Registered Mobile no</span>
            <br/>
            {nodata ? (
              <span style={{ color: "red", margin: 5 }}>No Service Found</span>
            ) : null}

            <div className="formdiv">
              <form onSubmit={onregsubmitmobno} id="create-course-form">
                <input
                  type="text"
                  name="mobno"
                  id="mobno"
                  placeholder="Mobile no"
                  required
                  pattern="[6789][0-9]{9}"
                  title="Enter Valid Mobile No"
                  onChange={onchangehandler}
                />
                <input
                  type="submit"
                  text="NEXT"
                  id="regsubmit"
                  name="regsubmit"
                />
              </form>
            </div>
          </div>
        </div>
      </>
    );
  };

  return (
    <>
      <Header />
      <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
      {reddash ? redirectdashboard() : divno()}
      <Footer />
    </>
  );
};

export default TraceBooking;
