import React, { useState, useEffect } from "react";
import Axios from "axios";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import "./DashBoard.css";
import Itemdiv from "./Itemdiv";
import urlinit from "../ServerAddress";
import Grid from "@material-ui/core/Grid";
import Button from "@material-ui/core/Button";
import MoneyIcon from "@material-ui/icons/Money";
import CancelIcon from "@material-ui/icons/Cancel";
import Referralwindow from "./Referralwindow";
import CircularProgress from "@material-ui/core/CircularProgress";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";
import {
  EmailShareButton,
  FacebookShareButton,
  TelegramShareButton,
  TwitterShareButton,
  WhatsappShareButton,
  TwitterIcon,
  TelegramIcon,
  WhatsappIcon,
  EmailIcon,
  FacebookIcon,
} from "react-share";
//const urlinit = "http://192.168.43.26:5000";

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const DashBoard = (props) => {
  const [data, setdata] = useState([]);
  const [promo, setpromo] = useState("");
  const [uid, setuid] = useState("");
  const [reftab, setreftab] = useState(false);
  const [name, setname] = useState("");
  const [backdrop, setBackdrop] = useState(true);

  const classes = useStyles();

  useEffect(() => {
    getdata();
  }, []);

  const commentsave = (serviceid, comment, formid, username, userid) => {
    Axios.post(urlinit + "/api/postcomments", {
      serviceid: serviceid,
      serviceform: formid,
      userid: userid,
      comment: comment,
      date: new Date().toLocaleString(),
      name: username,
    }).then((res) => {
      getdata();
    });
    // console.log(serviceid,comment,formid)
  };

  const commentupdate = (
    serviceid,
    comment,
    formid,
    username,
    userid,
    commentid
  ) => {
    Axios.post(urlinit + "/api/updatecomments", {
      serviceid: serviceid,
      serviceform: formid,
      userid: userid,
      comment: comment,
      date: new Date().toLocaleString(),
      name: username,
      comid: commentid,
    }).then((res) => {
      getdata();
    });
    // console.log(serviceid,comment,formid)
  };

  const getdata = () => {
    Axios.post(urlinit + "/api/getuserservicedetails", {
      mobno: localStorage.mobno,
    }).then((res) => {
      console.log(res.data);
      setdata(res.data);
      setuid(res.data[0].id);
      setname(res.data[0].name);
      setpromo(res.data[0].uniquepromo);
      console.log('promo--->',res.data);
      setBackdrop(false)
    });
  };

  const canceljob = (formid) => {
    Axios.post(urlinit + "/api/canceljob", {
      serviceform: formid,
    }).then((res) => {
      getdata();
    });
  };
  const startreferral = () => {
    Axios.post(urlinit + "/api/startreffering", {
      uid: uid,
    }).then((res) => {
      getdata();
    });
  };

  let url =
  "http://cosmaticservices.com/#/booknowformpage?ref=" + promo;

  const referral = () => {
    return (
      <div
        style={{
          padding: 10,
          background:"#ffffff",
          boxShadow: "1px 0px 30px rgb(146, 146, 153)",
        }}
      >
        <Grid container>
          <Grid item xs={12}>
            <Button
              variant="contained"
              onClick={() => {
                setreftab(false);
              }}
              color="secondary"
              endIcon={<CancelIcon />}
            >
              Close
            </Button>
          </Grid>
          <Grid item xs={12}>
            {promo===null ? (
              <Button
                variant="contained"
                onClick={() => {
                  startreferral();
                }}
                color="primary"
                endIcon={<MoneyIcon />}
              >
                Start
              </Button>
            ) : (<>
              <Grid item xs={12} sm={6}>
              <span style={{fontSize:18,padding:10}}>Share Link With your Friends</span>
              <Grid
                container
                direction="row"
                justify="space-around"
                alignItems="center"
              >
                <WhatsappShareButton
                  title="Get 5% Discount On your Car Services check it Now"
                  url={url}
                  separator="                "
                >
                  <WhatsappIcon size={48} />
                </WhatsappShareButton>
                <EmailShareButton
                  subject="Get 5% Discount On your Car Services check it Now"
                  url={url}
                >
                  <EmailIcon size={48} />
                </EmailShareButton>
                <FacebookShareButton
                  qoute="Get 5% Discount On your Car Services check it Now"
                  url={url}
                >
                  <FacebookIcon size={48} />
                </FacebookShareButton>
                <TelegramShareButton
                  title="Get 5% Discount On your Car Services check it Now"
                  url={url}
                >
                  <TelegramIcon size={48} />
                </TelegramShareButton>
                <TwitterShareButton
                  title="Get 5% Discount On your Car Services check it Now"
                  url={url}
                >
                  <TwitterIcon size={48} />
                </TwitterShareButton>
              </Grid>
            </Grid>
              <Referralwindow userid={uid} promo={promo} />
              </>
            )}
          </Grid>
        </Grid>
      </div>
    );
  };

  return (
    <>
      <Header />
      <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
      <div className="dashboard">
        
        {reftab ? (
          referral()
        ) : (
          <div
            style={{
              padding: 10,

              boxShadow: "1px 0px 30px rgb(146, 146, 153)",
              display: "flex",
              flexDirection: "row",
            }}
          >
            <Grid container>
              <Grid item xs={12}>
                <span
                  style={{
                    fontSize: 19,
                    fontWeight: 500,
                    
                  }}
                >
                  Welcome! {name}
                </span>
              </Grid>
              <Grid item xs={4}>
                <span style={{ fontSize: 18 }}>Save Money with referral</span>
              </Grid>
              <Grid item xs={4}>
                <Button
                  variant="contained"
                  onClick={() => {
                    setreftab(true);
                  }}
                  color="secondary"
                  endIcon={<MoneyIcon />}
                >
                  Referral
                </Button>
              </Grid>
            </Grid>
          </div>
        )}

        {data
          ? data.map((items, i) => {
              return (
                <Itemdiv
                  key={items.serv}
                  carcompany={items.carcompany}
                  carmodel={items.carmodel}
                  status={items.status}
                  promo={items.promo}
                  finalprice={items.finalprice}
                  date={items.date}
                  serviceid={items.serviceid}
                  address={items.address}
                  sername={items.sername}
                  formid={items.serv}
                  userid={items.id}
                  username={items.name}
                  comment={items.comment}
                  comdate={items.comdate}
                  comid={items.comid}
                  commentfun={commentsave}
                  commentupdate={commentupdate}
                  canceljob={canceljob}
                  seat={items.seat}
                />
              );
            })
          : null}
      </div>

      <Footer />
    </>
  );
};

export default DashBoard;
