import React from "react";
import Cancel from "./icon/cancel.png";
import Checked from "./icon/checked.png";
import Process from "./icon/process.png";
import "./DashBoard.css";
import Grid from "@material-ui/core/Grid";
import Divider from "@material-ui/core/Divider";
import Button from "@material-ui/core/Button";
import { makeStyles } from "@material-ui/core/styles";
import CancelIcon from "@material-ui/icons/Cancel";
import SendIcon from "@material-ui/icons/Send";
import EditIcon from "@material-ui/icons/Edit";

const useStyles = makeStyles((theme) => ({
  button: {
    margin: theme.spacing(1),
  },
}));

const Itemdiv = (props) => {
  const [edit, setedit] = React.useState(false);
  const [comment, setcomment] = React.useState(props.comment);
  const [serviceid] = React.useState(props.serviceid);
  const [formid] = React.useState(props.formid);
  const classes = useStyles();

  const srcimg = () => {
    if (props.status === 1) {
      return Process;
    }
    if (props.status === 2) {
      return Checked;
    }
    if (props.status === 3) {
      return Cancel;
    }
  };

  const reviewdiv = () => {
    return props.comment === null ? (
      <Grid container>
        <Grid item xs={8}>
          <textarea
            style={{ width: "100%" }}
            placeholder="Review Us (Max length 200 Characters)"
            rows="3"
            maxLength="200"
            onChange={(event) => {
              setcomment(event.target.value);
            }}
          />
        </Grid>
        <Grid item xs={4}>
          <Button
            variant="contained"
            onClick={() => {
              props.commentfun(
                serviceid,
                comment,
                formid,
                props.username,
                props.userid
              );
            }}
            color="primary"
            endIcon={<SendIcon />}
          >
            Save
          </Button>
        </Grid>
      </Grid>
    ) : edit ? (
      <Grid container>
        <Grid item xs={8}>
          <textarea
            style={{ width: "100%" }}
            placeholder="Review Us (Max length 200 Characters)"
            rows="3"
            maxLength="200"
            value={comment}
            onChange={(event) => {
              setcomment(event.target.value);
            }}
          />
        </Grid>
        <Grid item xs={4}>
          <Button
            variant="contained"
            className={classes.button}
            color="primary"
            onClick={() => {
              props.commentupdate(
                serviceid,
                comment,
                formid,
                props.username,
                props.userid,
                props.comid
              );
              setedit(false);
            }}
            endIcon={<SendIcon />}
          >
            Save&nbsp;
          </Button>
          <Button
            variant="contained"
            onClick={() => {
              setedit(false);
            }}
            color="secondary"
            endIcon={<CancelIcon />}
          >
            Close
          </Button>
        </Grid>
      </Grid>
    ) : (
      <Grid container>
        <Grid item xs={8}>
          <span style={{ fontSize: 16 }}> {props.comment}</span>
        </Grid>
        <Grid item xs={4}>
          <Button
            variant="contained"
            onClick={() => {
              setedit(true);
            }}
            color="primary"
            endIcon={<EditIcon />}
          >
            Edit
          </Button>
        </Grid>
        <Grid item xs={6}>
          <span style={{ fontSize: 14, fontWeight: 450 }}>{props.comdate}</span>
        </Grid>
      </Grid>
    );
  };

  const cancalbt = () => {
    return props.status === 1 ? (
      <Button
        variant="contained"
        color="secondary"
        className={classes.button}
        startIcon={<CancelIcon />}
        onClick={() => {
          props.canceljob(formid);
        }}
      >
        Cancel
      </Button>
    ) : (
      <Button color="secondary">Cancelled</Button>
    );
  };
  const getmrp = () => {
    if (props.serviceid === 1) {
      if (props.seat === 5) {
        return 2000;
      }
      if (props.seat === 7) {
        return 2500;
      }
    }
    if (props.serviceid === 2) {
      if (props.seat === 5) {
        return 800;
      }
      if (props.seat === 7) {
        return 1000;
      }
    }
    if (props.serviceid === 3) {
      if (props.seat === 5) {
        return 1000;
      }
      if (props.seat === 7) {
        return 1200;
      }
    }
    if (props.serviceid === 4) {
      if (props.seat === 5) {
        return 800;
      }
      if (props.seat === 7) {
        return 1000;
      }
    }
    if (props.serviceid === 5) {
      return 900;
    }
    if (props.serviceid === 6) {
      return 900;
    }
  };
  return (
    <div className="listdiv">
      <div style={{ marginBottom: 15 }}>
        <Grid container>
          <Grid item xs={4}>
            <span style={{ fontSize: 16 }}>Service ID :- {props.formid}</span>
          </Grid>
        </Grid>
        <Divider />
      </div>
      <Grid container>
        <Grid item xs={3}>
          <img src={srcimg()} alt="ss" className="imglist" />
        </Grid>
        <Grid item xs={8}>
          <Grid
            container
            direction="column"
            justify="space-evenly"
            alignItems="flex-start"
          >
            <span style={{ fontSize: 22, fontWeight: 500 }}>
              {props.carmodel}
            </span>
            <span>{props.carcompany}</span>
          </Grid>
        </Grid>
      

        <Grid item xs={12}>
          <Grid
            container
            direction="column"
            justify="flex-start"
            alignItems="flex-start"
          >
            <table id="tb1" > 
              <tbody>
                <tr id="tb">
                  <td id="tditm">Amount</td>
                  <td id="tditm">{getmrp()}₹</td>
                </tr>
                <tr id="tb" style={{ color: "green" }}>
                  <td id="tditm">
                    Discount <br />{" "}
                    {props.promo ? <>
                    <span style={{ whiteSpace: "nowrap",color:'black' }}>Promo Used:-</span>
                    <span style={{ color:'black' }}>{props.promo}</span>
                    </> : null}
                  </td>
                  <td id="tditm">{getmrp() - props.finalprice}₹</td>
                </tr>
                <tr id="tb">
                  <td id="tditm">Net Amt</td>
                  <td id="tditm">{props.finalprice}₹</td>
                </tr>
                <tr id="tb" style={{ fontSize: 20, fontWeight: 450 }}>
                  <td id="tditme">Final Amount</td>
                  <td id="tditme"> {props.finalprice}₹</td>
                </tr>
              </tbody>
            </table>
          </Grid>
        </Grid>

        <Grid item xs={12}>
          <Divider />
          <Grid
            container
            direction="column"
            justify="space-evenly"
            alignItems="flex-start"
          >
            <span style={{ fontWeight: 450, fontSize: 16 }}>Address:-</span>
            <span>{props.address}</span>
          </Grid>
          <Divider />
          <span>Registered On:- {props.date}</span>
        </Grid>
      </Grid>
      {props.status === 2 ? (
        <Grid item xs={12}>
          <Divider />
          <Grid
            container
            direction="column"
            justify="space-evenly"
            alignItems="flex-start"
          >
            <span style={{ fontSize: 18, fontWeight: 500 }}>Review :-</span>
            {reviewdiv()}
          </Grid>
        </Grid>
      ) : (
        cancalbt()
      )}
    </div>
  );
};

export default Itemdiv;
