import React, { useState, useEffect } from "react";
import "./BookNow.css";
import Header from "../Header/Header";
import Footer from "../Footer/Footer";
import Divider from "@material-ui/core/Divider";
import Axios from "axios";
import CircularProgress from "@material-ui/core/CircularProgress";
import Backdrop from "@material-ui/core/Backdrop";
import { makeStyles } from "@material-ui/core/styles";
import DashBoard from "./DashBoard";
import { Redirect } from "react-router-dom";
import urlinit from "../ServerAddress";
import BookNowPrincing from "./BookNowPricing"
const queryString = require("query-string");

//const urlinit = "http://192.168.43.26:5000";
//const mod2 = "https://safe-bastion-89212.herokuapp.com/";
//Define Style
const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const BookNow = (props) => {
  const [name, setName] = useState("");
  const [mobno, setMobno] = useState("");
  const [email, setEmail] = useState("");
  const [seat, setSeat] = useState("");
  const [carcomp, setCarcomp] = useState("");
  const [carmodel, setCarmodel] = useState("");
  const [promo, setPromo] = useState("");
  const [serviceid, setServiceid] = useState("");
  const [price, setPrice] = useState(0);
  const [discount, setDiscount] = useState(0);
  // const [discountprice, setDiscountprice] = useState(0);
  const [finalprice, setFinalprice] = useState(0);
  const [promostatus, setpromostatus] = useState("");
  const [address, setAddress] = useState("");
  const [isnew, setIsnew] = useState(true);
  const [mobdiv, setmobdiv] = useState(true);
  const [userid, setuserid] = useState("");
  const [backdrop, setBackdrop] = useState(false);
  const [reddash, setreddash] = useState(false);
  const classes = useStyles();

  const parsed = queryString.parse(props.location.search);

  useEffect(() => {
    finalamout();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [serviceid]);
  useEffect(() => {
    finalamout();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [seat]);
  useEffect(() => {
    finalamout();
    // eslint-disable-next-line react-hooks/exhaustive-deps
  }, [discount]);
  useEffect(() => {
    if (parsed.ref !== undefined) {
      Axios.post(urlinit + "/api/checkpromo", {
        promocode: parsed.ref,
      }).then((res) => {
        if (res.data[0] !== undefined) {
          setDiscount(res.data[0].discount);
          setpromostatus("");
        } else {
          if (parsed.ref !== "") {
            setpromostatus("Invalid Promo code");
          }
          setDiscount(0);
        }
        setPromo(parsed.ref);
      });
    }
      // eslint-disable-next-line react-hooks/exhaustive-deps
  }, []);
  const onchangehandler = (event) => {
    let nam = event.target.name;
    let val = event.target.value;

    if (nam === "name") {
      setName(val);
    }
    if (nam === "address") {
      setAddress(val);
    }
    if (nam === "email") {
      setEmail(val);
    }
    if (nam === "mobno") {
      setMobno(val);
    }
    if (nam === "carmodel") {
      setCarmodel(val);
    }
    if (nam === "carcompany") {
      setCarcomp(val);
    }
    if (nam === "servicetype") {
      if (seat !== "") {
        Axios.post(urlinit + "/api/price", {
          seat: seat,
          serviceid: val,
        }).then((res) => {
          if (res.data[0] !== undefined) {
            setPrice(res.data[0].price);
          }
          setServiceid(val);
        });
      } else {
        setServiceid(val);
      }
    }

    if (nam === "promocode") {
      if (val === "") {
        setpromostatus("");
      }

      Axios.post(urlinit + "/api/checkpromo", {
        promocode: val,
      }).then((res) => {
        if (res.data[0] !== undefined) {
          setDiscount(res.data[0].discount);
          setpromostatus("");
        } else {
          if (val !== "") {
            setpromostatus("Invalid Promo code");
          }
          setDiscount(0);
        }
        setPromo(val);
      });
    }

    if (nam === "seat") {
      if (serviceid !== "") {
        Axios.post(urlinit + "/api/price", {
          seat: val,
          serviceid: serviceid,
        }).then((res) => {
          if (res.data[0] !== undefined) {
            setPrice(res.data[0].price);
          }
          setSeat(val);
        });
      } else {
        setSeat(val);
      }
    }
  };

  const onregsubmit = (event) => {
    event.preventDefault();
    setBackdrop(true);
    Axios.post(urlinit + "/api/formsave", {
      name: name,
      mobno: mobno,
      date: new Date().toLocaleString(),
      email: email,
      seat: seat,
      carmodel: carmodel,
      carcomp: carcomp,
      promo: promo,
      serviceid: serviceid,
      finalprice: finalprice,
      address: address,
    }).then((res) => {
      setBackdrop(false);
      if (res.data === true) {
        setreddash(true);
      }
    });
  };
  const finalamout = () => {
    if (discount !== 0) {
      var dis = discount / 100;
      var fp = price - dis * price;
      setFinalprice(fp.toFixed(2));
    }
    if (discount === 0) {
      setDiscount(0);
      setFinalprice(price);
    }
  };

  const onregsubmitmobno = (event) => {
    event.preventDefault();
    setBackdrop(true);
    document.getElementById("create-course-form").reset();
    Axios.post(urlinit + "/api/getuser", {
      mobno: mobno,
    }).then((res) => {
      if (res.data[0] !== undefined) {
        setMobno(res.data[0].mobileno);
        setName(res.data[0].name);
        setEmail(res.data[0].email);
        setuserid(res.data[0].id);
        setAddress(res.data[0].address);
        setmobdiv(false);
        setIsnew(false);
        setBackdrop(false);
      } else {
        setmobdiv(false);
        setIsnew(true);
        setBackdrop(false);
      }
    });
  };
  const onregsubmitexistuser = (event) => {
    event.preventDefault();
    setBackdrop(true);
    Axios.post(urlinit + "/api/regsubmitexistuser", {
      userid: userid,
      seat: seat,
      date: new Date().toLocaleString(),
      carmodel: carmodel,
      carcomp: carcomp,
      promo: promo,
      serviceid: serviceid,
      finalprice: finalprice,
      address: address,
    }).then((res) => {
      setBackdrop(false);

      if (res.data === true) {
        setreddash(true);
      }
    });
  };

  const redirectdashboard = () => {
    localStorage.setItem("mobno", mobno);
    return (
      <>
        <Redirect to="/dashboard" Component={DashBoard} />
      </>
    );
  };
  const divno = () => {
    return (
      <>
        <div className="booknowmain" style={{ height: "50vh" }}>
          <div className="formdivp">
            <h2>BOOKING FORM</h2>
            <div className="formdiv">
              <form onSubmit={onregsubmitmobno} id="create-course-form">
                <input
                  type="text"
                  name="mobno"
                  id="mobno"
                  placeholder="Mobile no"
                  required
                  pattern="[6789][0-9]{9}"
                  title="Enter Valid Mobile No"
                  onChange={onchangehandler}
                />
                <input
                  type="submit"
                  text="NEXT"
                  id="regsubmit"
                  name="regsubmit"
                />
              </form>
            </div>
          </div>
        </div>
      </>
    );
  };

  const divexistform = () => {
    return (
      <>
        <div className="booknowmain">
          <div className="formdivp">
            <h2>BOOKING FORM</h2>
            <div className="formdiv">
              <form onSubmit={onregsubmitexistuser} id="create-course-form">
                <input
                  type="text"
                  name="name"
                  id="name"
                  placeholder="Name"
                  required
                  readOnly
                  value={name}
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="mobno"
                  id="mobno"
                  placeholder="Mobile no"
                  required
                  readOnly
                  value={mobno}
                  pattern="[6789][0-9]{9}"
                  title="Enter Valid Mobile No"
                  onChange={onchangehandler}
                />
                <input
                  type="email"
                  name="email"
                  id="email"
                  readOnly
                  value={email}
                  placeholder="Email"
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="carcompany"
                  id="carcompany"
                  placeholder="Car Brand"
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="carmodel"
                  id="carmodel"
                  placeholder="Car Model"
                  onChange={onchangehandler}
                />
                <select
                  name="seat"
                  id="seat"
                  required
                  placeholder="Select Seat"
                  onChange={onchangehandler}
                >
                  <option value="">Number of Seats</option>
                  <option value="5">5 Seats</option>
                  <option value="7">7+ Seats</option>
                </select>
                <select
                  name="servicetype"
                  id="servicetype"
                  required
                  placeholder="Select Service"
                  onChange={onchangehandler}
                >
                  <option value="">Service type</option>
                  <option value="1">Teflon Polish</option>
                  <option value="2">Dry cleaning</option>
                  <option value="3">Rubbing Polish</option>
                  <option value="4">Engine Coating</option>
                  <option value="5">Ac Duct Foam Cleaner</option>
                  <option value="6">Anti- Rat treatment</option>
                </select>
                <textarea
                  name="address"
                  placeholder="Enter Address"
                  id="address"
                  required
                  value={address}
                  rows="3"
                  onChange={onchangehandler}
                />
                <Divider />
                <input
                  type="text"
                  name="promocode"
                  id="promocode"
                  value={promo}
                  placeholder="Promo Code"
                  onChange={onchangehandler}
                />
                <p style={{ color: "red" }}>{promostatus}</p>
                <Divider />
                <table id="table">
                  <tbody>
                    <tr>
                      <td style={{ textAlign: "left", width: "70%" }}>Price</td>
                      <td style={{ textAlign: "left" }}>{price}</td>
                    </tr>
                  </tbody>
                  <tbody>
                    <tr>
                      <td style={{ textAlign: "left", width: "70%" }}>
                        Discount
                      </td>
                      <td style={{ textAlign: "left" }}>{discount}%</td>
                    </tr>
                  </tbody>
                  <tbody>
                    <tr>
                      <td style={{ textAlign: "left", width: "70%" }}>
                        {" "}
                        Final Amount
                      </td>
                      <td style={{ textAlign: "left" }}>{finalprice}</td>
                    </tr>
                  </tbody>
                </table>
                <Divider />
                <input
                  type="submit"
                  text="SUBMIT"
                  id="regsubmit"
                  name="regsubmit"
                />
                <input
                  type="reset"
                  onClick={() => {
                    setDiscount(0);
                    setFinalprice(0);
                    setPrice(0);
                  }}
                  text="RESET"
                  id="regreset"
                  name="regreset"
                />
              </form>
            </div>
          </div>
        </div>
      </>
    );
  };
  const divfullform = () => {
    return (
      <>
        <div className="booknowmain">
          <div className="formdivp">
            <h2>BOOKING FORM</h2>
            <div className="formdiv">
              <form onSubmit={onregsubmit} id="create-course-form">
                <input
                  type="text"
                  name="name"
                  id="name"
                  placeholder="Name"
                  required
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="mobno"
                  id="mobno"
                  placeholder="Mobile no"
                  required
                  readOnly
                  value={mobno}
                  pattern="[6789][0-9]{9}"
                  title="Enter Valid Mobile No"
                  onChange={onchangehandler}
                />
                <input
                  type="email"
                  name="email"
                  id="email"
                  placeholder="Email"
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="carcompany"
                  id="carcompany"
                  placeholder="Car Brand"
                  onChange={onchangehandler}
                />
                <input
                  type="text"
                  name="carmodel"
                  id="carmodel"
                  placeholder="Car Model"
                  onChange={onchangehandler}
                />
                <select
                  name="seat"
                  id="seat"
                  required
                  placeholder="Select Seat"
                  onChange={onchangehandler}
                >
                  <option value="">Number of Seats</option>
                  <option value="5">5 Seats</option>
                  <option value="7">7+ Seats</option>
                </select>
                <select
                  name="servicetype"
                  id="servicetype"
                  required
                  placeholder="Select Service"
                  onChange={onchangehandler}
                >
                  <option value="">Service type</option>
                  <option value="1">Teflon Polish</option>
                  <option value="2">Dry cleaning</option>
                  <option value="3">Rubbing Polish</option>
                  <option value="4">Engine Coating</option>
                  <option value="5">Ac Duct Foam Cleaner</option>
                  <option value="6">Anti- Rat treatment</option>
                </select>
                <textarea
                  name="address"
                  placeholder="Enter Address"
                  id="address"
                  required
                  rows="3"
                  onChange={onchangehandler}
                />
                <Divider />
                <input
                  type="text"
                  name="promocode"
                  id="promocode"
                  value={promo}
                  placeholder="Promo Code"
                  onChange={onchangehandler}
                />
                <p style={{ color: "red" }}>{promostatus}</p>
                <Divider />
                <table id="table">
                  <tr>
                    <td style={{ textAlign: "left", width: "70%" }}>Price</td>
                    <td style={{ textAlign: "left" }}>{price}</td>
                  </tr>
                  <tr>
                    <td style={{ textAlign: "left", width: "70%" }}>
                      Discount
                    </td>
                    <td style={{ textAlign: "left" }}>{discount}%</td>
                  </tr>
                  <tr>
                    <td style={{ textAlign: "left", width: "70%" }}>
                      {" "}
                      Final Amount
                    </td>
                    <td style={{ textAlign: "left" }}>{finalprice}</td>
                  </tr>
                </table>
                <Divider />
                <input
                  type="submit"
                  text="SUBMIT"
                  id="regsubmit"
                  name="regsubmit"
                />
                <input
                  type="reset"
                  onClick={() => {
                    setDiscount(0);
                    setFinalprice(0);
                    setPrice(0);
                  }}
                  text="RESET"
                  id="regreset"
                  name="regreset"
                />
              </form>
            </div>
          </div>
        </div>
      </>
    );
  };
  return (
    <>
      <Header />
      <Backdrop className={classes.backdrop} open={backdrop}>
        <CircularProgress color="inherit" />
      </Backdrop>
      {reddash
        ? redirectdashboard()
        : mobdiv
        ? divno()
        : isnew
        ? divfullform()
        : divexistform()}
        <BookNowPrincing/>
        <Footer />
    </>
  );
};

export default BookNow;
